import { createStyles, Theme, withStyles } from '@material-ui/core/styles';
import React from 'react';
import EnhancedTable from '../components/Enhanced/Table/Table';
import TableWrapper from '../components/TableWrapper';
import User from '../components/User';
import { SITE_TABLE } from '../graphql/mutation/local';
import { SITE_TABLE as SITE_TABLE_QUERY } from '../graphql/query/local';
import { SITES } from '../graphql/query/remote';
import { defaultState } from '../graphql/resolvers';
import { Routes } from '../Router';
import commanRender from '../utils/commanRender';
import { siteColumnData, siteExclude } from './Site';

const styles = (theme: Theme) => createStyles({
  root: {
    width: '100%',
    // padding: '0',
  },
  paper: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
  },
  fab: {
    position: 'fixed',
    width: '8rem',
    bottom: theme.spacing.unit * 2,
    right: theme.spacing.unit * 2,
  },
})

const ManagerDashboard = withStyles(styles)((props: any) => {
  const { classes, history, ...restProps } = props
  const { siteTable } = defaultState
  // updateQuery(data);
  return (
    <div>
      <User {...props}></User>
      <TableWrapper
        tableMutation={SITE_TABLE}
        tableQuery={SITE_TABLE_QUERY}
        dataQuery={SITES}
        defaultState={siteTable}
      >
        {(tableSettings: typeof siteTable, lmutate: Function, { loading, data, refetch }: IGraphqlData) => {
          return (
            <EnhancedTable
              {...restProps}
              loading={loading}
              data={data.sites}
              title={'Sites'}
              settings={siteTable}
              refetch={refetch}
              count={(data.sites.length && data.sites[0].count) || data.sites.length}
              columnData={siteColumnData}
              exclude={siteExclude}
              options={{
                selectable: false,
                onView: (e: any) => history.push(Routes.Site.detail({ id: e })),
              }}
              render={commanRender({ match: 'managerSpentAmount' })}
              mutate={async args => {
                await lmutate({ variables: args })
                // refetch();
              }}
            >
            </EnhancedTable>
          )
        }}
      </TableWrapper>
    </div>
  )
})

export default ManagerDashboard
