import React, { Component } from 'react'
import Query from './Query'
import { withStyles, createStyles, WithStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import Paper from '@material-ui/core/Paper'
import { USER } from '../graphql/query/remote'
import toCurrency from '../utils/toCurrency'
import { RENDER_VALUE } from '../utils/commanRender'
import { Theme } from '@material-ui/core/styles/createMuiTheme'

const styles = (theme: Theme) => createStyles({
  paper: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
  },
})

interface IUser extends WithStyles<typeof styles> {
  user: any
}

const User = withStyles(styles)(
  (props: IUser) => {
    const { classes, user } = props
    return (
      <Query query={USER} variables={{ id: user.id }}>
        {({ loading, error, data: { user } }: IGraphqlData) => {
          if (loading) {
            return (<span>loading...</span>)
          } else if (error) {
            return (<span>Error Occured!</span>)
          }
          return (
            <Paper className={classes.paper}>
              <Grid container={true} spacing={32}>
                <Grid item={true} container={true} direction='column' sm={6}>
                  <Grid item={true}>
                    <Typography variant='headline' className='ttc' color='primary'>
                      Username: {user.username}
                    </Typography>
                  </Grid>
                  <Grid item={true}>
                    <Typography variant='headline' className='ttc' color='secondary'>
                      Sites: {toCurrency(user.siteCount)}
                    </Typography>
                  </Grid>
                </Grid>
                <Grid item={true} container={true} direction='column' sm={6}>
                  <Grid item={true}>
                    <Typography variant='subheading'>
                      Total Received Amount (₹): <span style={{ color: RENDER_VALUE({ value: user.totalReceivedAmount, balance: false }).color || undefined}}>{RENDER_VALUE({ value: user.totalReceivedAmount }).value}</span>
                    </Typography>
                  </Grid>
                  <Grid item={true}>
                    <Typography variant='subheading'>
                      Total Spent Amount (₹): {toCurrency(user.spent)}
                    </Typography>
                  </Grid>
                  <Grid item={true}>
                    <Typography variant='subheading'>
                      Total Balance (₹): <span style={{ color: RENDER_VALUE({ value: user.balance, balance: false }).color || undefined }}>{RENDER_VALUE({ value: user.balance }).value}</span>
                    </Typography>
                  </Grid>
                </Grid>
              </Grid>
            </Paper>
          )
        }}
      </Query>
    )
  },
)

export default User
