import React, { PureComponent } from 'react'
import Mutation from '../components/Mutation'
import Grid from '@material-ui/core/Grid'
import { withStyles, createStyles, Theme, WithStyles } from '@material-ui/core/styles'
import Query from '../components/Query'
import { defaultState } from '../graphql/resolvers'
import { Routes } from '../Router'

import { SITES } from '../graphql/query/remote'
import { SITE_TABLE as SITE_TABLE_QUERY } from '../graphql/query/local'

import { SITE_TABLE } from '../graphql/mutation/local'
import { DELETE_SITES } from '../graphql/mutation/remote'

import commanRender from '../utils/commanRender'
import EnhancedTable from '../components/Enhanced/Table/Table'

const styles = (theme: Theme) => createStyles({
  root: {
    width: '100%',
  },
  fab: {
    position: 'fixed',
    width: '4rem',
    bottom: theme.spacing.unit * 2,
    right: theme.spacing.unit * 2,
  },
})

export const siteColumnData = [
  { id: 'createdAt', numeric: false, disablePadding: false, colSpan: 1, label: 'Created At' },
  { id: 'name', numeric: false, disablePadding: false, colSpan: 1, label: 'Name' },
  { id: 'location', numeric: false, disablePadding: false, colSpan: 1, label: 'Location' },
  { id: 'entryCount', numeric: false, disablePadding: false, colSpan: 1, label: 'Total Entries' },
  { id: 'manager', numeric: false, disablePadding: false, colSpan: 1, label: 'Manager' },
  { id: 'managerSpentAmount', numeric: false, disablePadding: false, colSpan: 1, label: 'Manager Spent Amount (₹)' },
  { id: 'Total Cost', numeric: false, disablePadding: false, colSpan: 1, label: 'Total Cost (₹)' },
]
export const siteExclude = ['id', 'count', 'total', 'createdAt', 'updatedAt', '__typename']

interface ISiteProps extends WithStyles<typeof styles> {
  history?: any
}

const Site = withStyles(styles)((props: ISiteProps) => {
  const { classes, history, ...restProps } = props
  return (
    <div>
      <Grid container={true} className={classes.root}>
        <Grid item={true} xs={12}>
          <Mutation mutation={SITE_TABLE}>
            {(lmutate: Function, args: IGraphqlData) => {
              return (
                <Query query={SITE_TABLE_QUERY}>
                  {({ data: { siteTable } }: IGraphqlData) => {
                    return (
                      <Mutation mutation={DELETE_SITES}>
                        {(deleteSites: Function) => {
                          const { page, rowsPerPage } = siteTable
                          return (
                            <Query query={SITES} variables={{ limit: rowsPerPage, skip: page * rowsPerPage }}>
                              {
                                ({ loading, data, refetch }: IGraphqlData) => {
                                  // refetch();
                                  return (
                                    <EnhancedTable
                                      {...restProps}
                                      loading={loading}
                                      data={data.sites}
                                      title='Sites'
                                      settings={siteTable}
                                      count={(data.sites.length && data.sites[0].count) || data.sites.length}
                                      columnData={siteColumnData}
                                      exclude={siteExclude}
                                      options={{
                                        onView: (e: any) => history.push(Routes.Site.detail({ id: e })),
                                        onEdit: (e: any) => history.push(Routes.Site.update({ id: e, edit: true })),
                                        onDelete: async (args: any) => {
                                          console.log('calling deleteMutate function...')
                                          await deleteSites({ variables: args })
                                          await lmutate({ variables: defaultState.siteTable })
                                          refetch()
                                        },
                                        onAdd: () => history.push(Routes.Site.create()),
                                      }}
                                      render={commanRender({ match: 'managerSpentAmount' })}
                                      mutate={async args => {
                                        await lmutate({ variables: args })
                                        // refetch();
                                      }}
                                    >
                                    </EnhancedTable>
                                  )
                                }}
                            </Query>
                          )
                        }}
                      </Mutation>
                    )
                  }}
                </Query>
              )
            }}
          </Mutation>
        </Grid>
      </Grid>
    </div>
  )
})

export default Site
